import React, { Component } from 'react'
import { Menu, } from 'antd';
import { Link, withRouter } from "react-router-dom";
import { myblogData, gojsData } from "../../assets/data";
import "./index.less";

const { SubMenu } = Menu;
const selectedKeyMap = {
    [myblogData.baseHref]: myblogData.title,
    [gojsData.baseHref]: gojsData.title,
};
for (let i = 0; i < gojsData.catalog.length; i++) {
    selectedKeyMap[gojsData.catalog[i].href] = gojsData.catalog[i].title
}
class index extends Component {
    render() {
        const pathSnippets = this.props.location.pathname;
        return (
            <Menu
                mode={this.props.mode}
                theme="light"
                id="menu"
                selectedKeys={selectedKeyMap[pathSnippets]}
            >
                <Menu.Item key={myblogData.title}>
                    <Link to={myblogData.baseHref}>{myblogData.title}</Link>
                </Menu.Item>
                <SubMenu
                    key={gojsData.title}
                    title={<span>{gojsData.title}</span>}
                    onTitleClick={() => { this.props.history.push("/gojs") }}
                    style={selectedKeyMap[pathSnippets] === "GoJS 教程" ? { background: "#e6f7ff", color: "#1890ff" } : null}
                >
                    {Object.keys(gojsData.section).map((item, index) => {
                        let catalog = gojsData.catalog.slice(gojsData.section[item][0], gojsData.section[item][1] + 1)
                        return (
                            catalog.length > 1 ?
                                <SubMenu
                                    key={item}
                                    title={<span>{item}</span>}
                                    popupClassName="submenu-overflow"
                                >
                                    {catalog.map((item2, index2) => {
                                        return (
                                            <Menu.Item key={item2.title}><Link to={item2.href}>{item2.title}</Link></Menu.Item>
                                        )
                                    })}
                                </SubMenu>
                                :
                                <Menu.Item key={catalog[0].title}><Link to={catalog[0].href}>{catalog[0].title}</Link></Menu.Item>
                        )
                    })}
                </SubMenu>
            </Menu>
        )
    }
}

export default withRouter(index);